@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
html, body, #root {
  width: 100%;
  height: auto;
  margin: 0;
  overflow-x:hidden;
  background:#fff;
  font-family: 'Source Code Pro', monospace;
}
.nav {
    width: 90%;
    height: 70px;
    background: #4d4657;
    position: relative;
    border-radius: 5px;
    margin: 100px auto;
    overflow:hidden;
}
.navBtn {
    font-family: 'Source Code Pro', monospace;
    position:relative;
    width: 16.666%;
    height: 100%;
    background-color: #4d4657;
    border:none;
    color: #fff;
    font-size: 15px;
    overflow:hidden;
    cursor:pointer;
    transition: 0.2s ease-in-out;
    outline:none;
}
.navBtn:hover {
    background-color: #2b2534;
}
.content {
    width: 100%;
    background:#fff;
}
.content-inner {
    display:flex;
    flex-direction:row;
}
.title {
    font-size: 30px;
}
.last-ten-table {
    width: 700px;
    text-align:left;
}
.change-interval {
    float:right;
}
.common-table {
    width: 400px;
    text-align:left;
}
.chart-container {
    width: 800px;
}
.change-interval, .change-accessor {
    height: 100px;
    width: 200px;
}
.app {
  width: 100%;
  height: 100%;
  position:relative;
  background:#fff;
}
.content-container {
  width: 90%;
  position:relative;
  margin: 0 auto;
}
